:root {
  --text-dark-color: #111111;
  --text-light-color: #ffffff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-color: #111111;
  --icon-dark-transparent-color: #1111111a;
  --icon-orange-color: #fd9222;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #ffffff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --border-dark-transparent-color: #ffffff80;

  --transition-duration: 250ms;
  --transition-tf: cubic-bezier(0.4, 0, 0.2, 1);
}

.unique-components {
  padding-top: 72px;
  padding-bottom: 72px;
}
@media screen and (min-width: 768px) {
  .unique-components {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}
@media screen and (min-width: 1200px) {
  .unique-components {
    padding-top: 128px;
    padding-bottom: 128px;
  }
}

.components-flex-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 335px;
}
@media screen and (min-width: 320px) and (max-width: 374px) {
  .components-flex-box {
    min-width: 290px;
  }
}

.components-secondary-title {
  color: var(--text-dark-color);
  /* font-family: Montserrat; */
  font-size: 20px;
  font-weight: 600;
  line-height: 24px; /* 120% */
  letter-spacing: -0.6px;
  text-transform: uppercase;
  font-style: normal;
  text-align: center;
  margin-bottom: 24px;
  max-width: 283px;
}
@media screen and (min-width: 320px) and (max-width: 374px) {
  .components-secondary-title {
    max-width: 290px;
  }
}
@media screen and (min-width: 768px) {
  .components-secondary-title {
    font-size: 24px;
    line-height: 28px; /* 116.667% */
    letter-spacing: -0.72px;
    margin-bottom: 28px;
    max-width: 400px;
  }
}

.highlight {
  color: var(--text-orange-color);
}

.components-paragraph {
  color: var(--text-dark-color);
  /* font-family: Montserrat; */
  font-size: 14px;
  font-weight: 400;
  line-height: 18px; /* 128.571% */
  letter-spacing: -0.28px;
  font-style: normal;
  text-align: center;
  justify-content: center;
  margin-bottom: 32px;
  width: 300px;
}
@media screen and (min-width: 320px) and (max-width: 374px) {
  .components-paragraph {
    max-width: 290px;
  }
}
@media screen and (min-width: 768px) {
  .components-paragraph {
    margin-bottom: 50px;
    width: 450px;
  }
}

.unique-components-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding-left: 0;
  gap: 20px;
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .unique-components-list {
    /* width: calc(100vw + 15px); */
    gap: 32px;
  }
  /* Chocolate */
  .unique-components-item:nth-child(1) {
    order: 0;
  }
  /* Milk */
  .unique-components-item:nth-child(2) {
    order: 1;
  }
  /* Nuts */
  .unique-components-item:nth-child(3) {
    order: 2;
  }
  /* Raisins */
  .unique-components-item:nth-child(4) {
  }
}
@media screen and (min-width: 1200px) {
  .unique-components-list {
    gap: 19px;
  }
}

.unique-components-item {
}
@media screen and (min-width: 768px) {
  .unique-components-item {
  }
}

.flip-card {
  background-color: transparent;
  width: 335px;
  height: 270px;
  border-radius: 15px;
  border: 1px solid transparent;
  align-items: center;
  overflow: visible;
  perspective: 2000px;
}
@media screen and (min-width: 320px) and (max-width: 374px) {
  .flip-card {
    max-width: 290px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media screen and (min-width: 1200px) {
  .flip-card {
    width: 270px;
  }
}

/* This container is needed to position the front and back side */
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.flip-card-front {
  background: var(--background-white-color);
  border-radius: 15px;
  border: 1px solid var(--text-orange-color);
  color: var(--text-dark-color);
}

.unique-components-link {
  text-decoration: none;
  list-style: none;
}

.flip-card-front > img {
  margin-top: 28px;
  margin-left: 56px;
}
@media screen and (min-width: 320px) and (max-width: 374px) {
  .flip-card-front > img {
    margin-left: 31px;
  }
}
@media screen and (min-width: 1200px) {
  .flip-card-front > img {
    margin-left: 28px;
  }
}

@media screen and (max-width: 1199px) {
  .img-desktop {
    display: none;
  }
}
@media screen and (min-width: 1200px) {
  .img-mobile {
    display: none;
  }
}

.component-name {
  color: var(--text-dark-color);
  /* font-family: Montserrat; */
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px; /* 128.571% */
  letter-spacing: -0.28px;
  text-transform: uppercase;
  margin-top: 25px;
  margin-left: 28px;
}

/* Style the back side */
.flip-card-back {
  background: var(--background-white-color);
  color: var(--text-light-color);
  transform: rotateY(180deg);
  border-radius: 15px;
  border: 1px solid var(--text-orange-color);
  padding: 28px;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .flip-card-back {
    padding: 20px;
  }
}
@media screen and (min-width: 1200px) {
  .flip-card-back {
    display: block;
  }
}

.overlay-name {
  color: var(--text-dark-color);
  /* font-family: Montserrat; */
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -0.54px;
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 8px;
}

.overlay-tags {
  display: flex;
  flex-direction: row;
  gap: 6px;
  margin-bottom: 14px;
}

.overlay-tag-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px 16px;
  gap: 4px;
  border-radius: 35px;
  border: 1px solid var(--text-orange-color);
  background: var(--background-light-color);
}

.overlay-tag {
  color: var(--text-dark-color);
  /* font-family: Montserrat; */
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px; /* 120% */
}

.overlay-paragraph {
  color: var(--text-dark-color);
  /* font-family: Montserrat; */
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px; /* 128.571% */
  letter-spacing: -0.28px;
  width: 275px;
}
@media screen and (min-width: 320px) and (max-width: 374px) {
  .overlay-paragraph {
    max-width: 240px;
  }
  .overflow-ellipsis {
    display: block;
    text-wrap: wrap;
    width: 240px;
    height: 20px;
    overflow-x: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .overflow-ellipsis:hover {
    overflow-x: visible;
    white-space: normal;
    text-overflow: '';
  }
}

@media screen and (min-width: 1200px) {
  .overlay-paragraph {
    width: 230px;
    height: 140px;
  }
  .overflow-ellipsis {
    display: block;
    text-wrap: wrap;
    width: 220px;
    height: 20px;
    overflow-x: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .overflow-ellipsis:hover {
    overflow-x: visible;
    white-space: normal;
    text-overflow: '';
  }
}
