@media screen and (min-width: 375px) {
  .backdrop {
    position: fixed;
    inset: 0 0 auto;
    /*width: 100%;
        height: 100%;*/
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /*padding: 72px 24px 24px 24px;*/
    background: rgba(46, 47, 66, 0.4);
    opacity: 1;
    pointer-events: all;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
      visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
  }

  .reviews-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    /*transform:translate (-50%, -50%);*/
    /*position: fixed;
        inset: 0 0 auto;*/
    min-width: 311px;
    min-height: 471px;
    border-radius: 15px;
    flex-shrink: 0;
    /*min-height: 100vh;*/
    padding: 32px 32px 32px 24px;
    /*display: flex;
        justify-content: center;
        align-items: center;
        opacity: 1;
        pointer-events: all;
        top: 0;
        left: 0;
        width: 80%;
        height: 80%;*/
    background-color: #fafafa;
    /*transform: translate(-100%, -100%);
        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);*/
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
      visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .is-hidden {
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    transform: translateX(-50%) translateY(-50%) scale(0) rotate(0deg) skew(40deg, 40deg);
  }

  .modal-contacts {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 18px;
  }

  .modal-div {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .modal-icon-box {
    position: relative;
    display: flex;
    align-items: center;
  }

  .modal-label {
    /*position: fixed;
        padding-left: 24px;*/
    display: block;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    display: block;
    margin-bottom: 4px;
    line-height: 1.17;
    letter-spacing: 0.04em;
    /*color: #8E8F99;*/
    color: #4040404d;
  }

  .modal-label-phone {
    position: fixed;
    padding-top: 2px;
    padding-left: 74px;
    display: block;
    color: #000;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    margin-bottom: 4px;
    line-height: 1.17;
    letter-spacing: 0.04em;
  }

  .modal-input {
    width: 100%;
    /*height: 55px;
        color: var(--DARK);*/
    height: 66px;
    resize: none;
    padding: 24px 22px;
    font-family: Montserrat;
    font-size: 18px;
    line-height: normal;
    letter-spacing: -0.36px;
    color: #4040404d;
    background-color: transparent;
    border: 1px solid #4040404d;
    border-radius: 13px;
    outline: transparent;
    transition-property: border-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  .modal-input::placeholder {
    color: rgba(64, 64, 64, 0.3);
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.36px;
  }

  .modal-input-phone {
    width: 100%;
    height: 66px;
    resize: none;
    padding-left: 133px;
    padding-top: 17px;
    padding-bottom: 17px;
    font-family: Montserrat;
    font-size: 18px;
    line-height: normal;
    letter-spacing: -0.36px;
    color: #4040404d;
    background-color: transparent;
    /*border: 1px solid #4040404D;*/
    border: 1.5px solid #4040404d;
    border-radius: 13px;
    outline: transparent;
    transition-property: border-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  .modal-input-phone::placeholder {
    color: rgba(64, 64, 64, 0.3);
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.36px;
  }

  .textarea::placeholder {
    color: rgba(64, 64, 64, 0.3);
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.36px;
  }

  .modal-svg {
    position: absolute;
    left: 24px;
    top: 50%;
    width: 32px;
    height: 24px;
    transform: translateY(-50%);
    transition-property: fill;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  .textarea {
    height: 137px;
    resize: none;
    padding: 24px 22px;
    font-family: Montserrat;
    font-size: 18px;
    line-height: normal;
    letter-spacing: -0.36px;
    color: #4040404d;
    background-color: transparent;
    border: 1.5px solid #4040404d;
    border-radius: 13px;
    outline: transparent;
    transition-property: border-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  .close-button {
    position: absolute;
    cursor: pointer;
    border-radius: 50%;
    border: none;
    width: 24px;
    height: 24px;
    right: 24px;
    top: 24px;
    padding: 0;
    color: #2e2f42;
    background-color: #fafafa;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
      border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  /*.close-button:hover,
    .close-button:focus {
        background-color: #FAFAFA;
        border: none;
        fill: #FD9222;
    }*/

  .close-svg {
    fill: #111111;
    /*stroke: #111111;*/
  }

  .close-svg:hover,
  .close-svg:focus {
    fill: #fd9222;
    stroke: #fd9222;
  }

  .close-button-svg {
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
  }

  .backdrop-header {
    color: #111;
    width: 255px;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.2;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    margin-bottom: 40px;
    margin-top: 0px;
    margin-right: 0px;
  }

  .send-button {
    display: inline-flex;
    padding: 14px 40px;
    align-items: flex-start;
    color: #ffffff;
    gap: 14px;
    background-color: #fd9222;
    border: #fd9222;
    border-radius: 100px;
    color: #fff;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    cursor: pointer;
  }
}

@media screen and (max-width: 768px) {
  .reviews {
    background-color: #fafafa;
    max-width: 768px;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 72px;
  }

  .main-reviews-div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }

  .reviews-div-child {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    /*justify-content: flex-end;*/
    align-items: center;
    position: relative;
    width: 335px;
    height: 200px;
    border-radius: 15px;
    border: 1px solid rgba(17, 17, 17, 0.1);
    background: #fff;
    box-shadow: 0px 9.04932px 26.57166px 0px rgba(215, 228, 249, 0.25);
  }

  .tablet-hide {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    display: none;
  }

  .desktop-hide {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    display: none;
  }

  .reviews-heading {
    color: #111;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    margin-bottom: 74px;
  }

  .heading-span {
    color: #fd9222;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.6px;
    text-transform: uppercase;
  }

  .reviews-image {
    background-color: transparent;
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    top: -40px;
  }

  .reviews-name {
    color: #111;
    text-align: center;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 133.333%;
    margin-top: 18px;
    margin-bottom: 24px;
  }

  .reviews-paragraph {
    width: 299px;
    color: rgba(17, 17, 17, 0.7);
    text-align: center;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 128.571%;
    letter-spacing: -0.28px;
    margin-top: 0px;
    margin-bottom: 42px;
  }

  .reviews-svg-list {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 13px;
    padding: 0;
    margin: 0;
  }

  .reviews-button {
    width: 165px;
    height: 45px;
    display: flex;
    padding: 12px 28px;
    gap: 14px;
    background-color: transparent;
    border-radius: 100px;
    border: 1px solid #fd9222;
    margin-top: 32px;
    cursor: pointer;
    color: #111;
    text-align: center;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  .review-button:hover,
  .review-button:focus {
    background-color: #fd9222;
    color: #fff;
  }
}

