body {
  color: var(--text-dark-color);
  background-color: var(--background-light-color);
  font-family: Montserrat, sans-serif;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-color: #111;
  --icon-dark-transparent-color: #1111111a;
  --icon-orange-color: #fd9222;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #fff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --border-dark-transparent-color: #ffffff80;
  --transition-duration: .25s;
  --transition-tf: cubic-bezier(.4, 0, .2, 1);
}

* {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-color: #111;
  --icon-dark-transparent-color: #1111111a;
  --icon-orange-color: #fd9222;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #fff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --border-dark-transparent-color: #ffffff80;
  --transition-duration: .25s;
  --transition-tf: cubic-bezier(.4, 0, .2, 1);
}

.header-container {
  max-width: 335px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-logo {
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
  display: inline-flex;
}

.header-navigation {
  justify-content: space-around;
  align-items: center;
  gap: 138px;
  padding: 20px 0 14px;
  display: flex;
}

.header-logo-link {
  color: var(--text-dark-color);
  letter-spacing: -.03em;
  text-transform: uppercase;
  align-items: center;
  font-family: Montserrat;
  font-size: 14px;
  font-style: italic;
  font-weight: 700;
  line-height: 1.3;
  text-decoration: none;
  display: flex;
}

.header-logo-choco {
  padding: 0 0 0 4px;
  font-style: normal;
}

.header-hamburger {
  align-items: center;
  margin: 0 0 0 auto;
}

.header-hamburger:hover, .header-hamburger:focus {
  cursor: pointer;
  fill: currentColor;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.menu-toggle {
  background-color: #0000;
  border: none;
  margin: 0;
  padding: 0;
}

.menu-container {
  width: 220px;
  height: 100vh;
  background-color: var(--background-dark-color);
  z-index: 999;
  flex-direction: column;
  justify-content: space-between;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.header-close-menu {
  cursor: pointer;
  fill: var(--background-white-color);
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.header-close-icon {
  width: 28px;
  height: 28px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.header-close-menu:hover > .header-close-icon, .header-close-menu:focus > .header-close-icon {
  width: 40px;
  height: 40px;
  fill: var(--icon-orange-color);
}

.header-mobile-navigation {
  padding: 0;
  position: absolute;
  top: 136px;
  left: 50px;
}

.header-mobile-nav-list {
  flex-direction: column;
  gap: 24px;
  margin: 0;
  list-style: none;
  display: flex;
}

.header-nav-li-mobile {
  text-decoration: none;
}

.header-nav-link-menu {
  color: var(--background-white-color);
  letter-spacing: .02em;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: relative;
}

.header-nav-link-menu:hover, .header-nav-link-menu:focus {
  color: var(--icon-orange-color);
}

.header-link-underline {
  text-decoration: none;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header-link-underline:after {
  content: "";
  height: 1px;
  background-color: var(--background-orange-color);
  width: 100%;
  border-bottom: 2px;
  border-radius: 2px;
  transition-property: width;
  transition-timing-function: linear;
  display: block;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.header-sm-mobile {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.header-mobile-sm-list {
  gap: 18px;
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.header-mobile-icon {
  fill: var(--background-white-color);
}

.header-sm-mobile-link {
  width: 100%;
  height: 100%;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header-sm-mobile-link:hover > .header-mobile-icon, .header-sm-mobile-link:focus > .header-mobile-icon {
  fill: var(--icon-orange-color);
  cursor: pointer;
}

.menu-container .header-mobile-nav-list {
  max-width: 200px;
  justify-content: start;
}

.address-link-mobile {
  flex-direction: column;
  gap: 40px;
  margin-bottom: 48px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-container {
    max-width: 704px;
    align-items: center;
  }

  .header-navigation {
    gap: 503px;
    padding: 24px 0;
  }

  .menu-container {
    width: 290px;
  }

  .header-close-menu {
    top: 24px;
    right: 32px;
  }

  .header-sm-mobile {
    bottom: 24px;
    left: 32px;
  }
}

@media screen and (max-width: 1157px) {
  .header-menu-list, .header-menu-link, .header-sm-icon, .header-sm-icons, .header-sm-list, .header-icon-choco-big {
    display: none;
  }
}

