  padding-bottom: 12px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.24;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-submit:hover {
  color: var(--icon-orange-color);
  background-color: var(--background-button-color);
